<template>
  <div class="banner" @mouseenter="enter" @mouseleave="leave">
		<div class="box">
			<transition-group
        tag="ul"
        class='ad'
        name="image"
       	:enter-active-class="enterTransitionName"
        :leave-active-class="leaveTransitionName">
        <li v-for='(image,index) in imgList' :key='index' v-show="index === mark">
            <a href="javascript:;">
                <img :src="image">
            </a>
        </li>
		  </transition-group>
		</div>
		<div class="hd" v-if="isPoint">
	    <span v-for="(item,index) in imgList.length" :class="{'active':index === mark}"
	     @click="change(index)" :key="index"></span>
    </div>
		<div class="arrow" v-if="isPage">
	        <span class="prev" @click="prev">&lt;</span>
	        <span class="next" @click="next">&gt;</span>
	    </div>
	</div>
</template>

<script>
export default {
  name: 'BackToTop',
  props: {
  	aPlay: {
  		type: Boolean,
  		default: true
  	},
  	playTime: {
  		type: Number,
  		default: 3000
  	},
  	imgList: {
  		type: Array,
  		default: []
  	},
  	selectMark: {
  		type: Number,
  		default: 0
  	},
  	isPage: {
  		type: Boolean,
  		default: true
  	},
  	isPoint: {
  		type: Boolean,
  		default: true
  	},
    enterTransitionName: {
      type: String,
      default: 'animated fadeIn'
    },
    leaveTransitionName: {
      type: String,
      default: 'animated fadeOut'
    },
  },
  data() {
    return {
    	mark: 0,
	    time:null
    }
  },
  methods: {
    change(i){
	    this.mark = i;
		},
		prev(){
	    this.mark--;
	    if(this.mark === -1){
	        this.mark = this.imgList.length-1;
	        return;
	    }
		},
		next(){
	    this.mark++;
	    if(this.mark === this.imgList.length){
	        this.mark = 0;
	        return;
	    }
		},
		autoPlay(){
	    this.mark++;
	    if(this.mark === this.imgList.length){
	        this.mark = 0;
	        return;
	    }
		},
		play(){
	    this.time = setInterval(this.autoPlay,this.playTime);
		},
		enter(){
			if(this.aPlay){
	    	clearInterval(this.time);
	   	}
		},
		leave(){
			if(this.aPlay){
	    	this.play();
	   	}
		}
  },
  mounted: function(){
  	this.mark = this.selectMark;
  	if(this.aPlay){
    	this.play();
  	}
  }
}
</script>
<style scoped>
.banner {
	width: 100%;
	height: 527px;
	position: relative;
	overflow: hidden;
	padding-top: 10px;
	background: url(/static/images/color_line.png) repeat-y center center;
}
.banner .box {
	position: absolute;
	width: 1920px;
	height: 527px;
	left: 50%;
	margin-left: -960px;
	overflow: hidden;
	z-index: 0;
}
.banner .box .ad {
	width: 100%;
	height: 100%;
}
.banner .box .ad li {
	height: 100%;
	width: 100%;
	position: absolute;
}
.banner .hd {
	position: absolute;
	bottom: 27px;
	left: 50%;
}
.banner .hd span {
	position: relative;
	left: -50%;
	float: left;
	width: 35px;
	height: 8px;
	background-color: #da3c81;
	margin: 0 5px;
	cursor: pointer;
	z-index: 1;
	opacity: 0.27;
}
.banner .hd span.active {
	opacity: 1;
}
/*左右箭头*/
.banner .arrow span {
	position: absolute;
	display: block;
	width: 60px;
	height: 60px;
	line-height: 55px;;
	top: 50%;
	margin-top: -30px;
	color: #333;
	font-size: 45px;
	text-align: center;
	cursor: pointer;
	transition: all 0.6s;
}
.banner .arrow span{
	position: absolute;
	width: 50px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	background-color: rgba(0,0,0,.1);
	font-size: 20px;
	color: #ffffff;
	top: 50%;
	margin-top: -25px;
	cursor: pointer;
	font-family: "宋体";
} 
.banner .arrow span:hover{
	background-color: rgba(0,0,0,.5);
}
.banner .arrow span.prev {
	left: 0;
}
.banner .arrow span.next {
	right: 0;
}
</style>


