import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
  	{
      path: '/',
      name: 'index',
      component: () => import('@/views/index')
    },
    {
      path: '/index',
      name: 'index',
      component: () => import('@/views/index')
    },
    {
      path: '/process',
      name: 'process',
      component: () => import('@/views/process')
    },
    {
      path: '/case',
      name: 'cases',
      component: () => import('@/views/case')
    },
    {
      path: '/news',
      name: 'news',
      component: () => import('@/views/news')
    },
    {
      path: '/packages',
      name: 'packages',
      component: () => import('@/views/packages')
    },
    {
      path: '/details/:uid',
      name: 'details',
      component: () => import('@/views/details')
    },
    {
      path: '/demo',
      name: 'vuexDemo',
      component: () => import('@/views/vuexDemo')
    },
    {
      path: '/strap',
      name: 'strap',
      component: () => import('@/views/strap')
    }
  ]
})


