<template>
	<div class="header">
		<div class="middle">
			<div class="logo">
				<a href="#"> <img src="../assets/images/logo.png" /> </a>
			</div>
			<div class="nav">
				<ul>
					<template v-for="(item,$index) in getData">
						<li :class="{'active':$index === curIndex}" @click="loadActive($index)">
							<router-link :to="item.name">{{ item.value }}</router-link>
						</li>
					</template>
				</ul>
			</div>
		</div>
  		<banner-ani :aPlay="false" :imgList="img" enterTransitionName="animated rollIn" leaveTransitionName="animated rollOut"></banner-ani>
	</div>
</template>

<script>
	import BannerAni from '@/components/BannerAnimate'
	import { mapState,mapGetters,mapMutations,mapActions  } from 'vuex'
	export default {
		name: 'header',
  		components: { BannerAni },
  		 data() {
		    return {
			    img:[
			        'static/images/banner1.jpg',
			        'static/images/banner2.jpg',
			        'static/images/banner3.jpg'
			    ]
		    }
		  },
		computed: {
			//得到数据
			...mapState({
				curIndex: state => state.header.curIndex,
			}),
			...mapGetters([
				'getData'
		    ])
		},
		methods: {
			//点击切换
			...mapMutations({
				loadActive: 'changeActive',
			}),
//			...mapActions({
//				loadActive: 'changeNav',
//			}),
			//设置数据
//			...mapMutations([
//				'setData'
//			])
		},
		mounted: function(){
			var _this = this;
			this.$store.dispatch('LOAD_NAV_LIST');
			//get方法获取nav数据，触发setData
//			this.$axios.get('../static/navData.json')
//			.then(function (response) {
//			  	_this.$store.commit('setData', response.data);
//			})
//			.catch(function (error) {
//			    console.log(error);
//		  	});
//			this.$http.get("../static/navData.json").then(
//	            (res) => {
//			    	this.$store.commit('setData', res.body)
//	            },(res) => {
//	            	console.log(res)
//	            }
//	        );
			this.$nextTick(() => {
				AOS.init({
					easing: 'ease',
					duration: 1000
				});
			})
		}
	}
</script>


