import axios from 'axios'
const state = {
	navActive: [],
    curIndex: 0
}
const getters = {
	getData: state => {
		return state.navActive;
	}
}
const mutations = {
	changeActive (state,$index){
		state.curIndex = $index;
	},
    setData (state,arr){
    	state.navActive = arr;
    }
}
const actions = {
    changeNav ({commit},$index) {
      	commit('changeActive',$index)
    },
    LOAD_NAV_LIST: function ({ commit }) {
      	axios.get('static/navData.json')
		.then(function (response) {
		  	commit('setData', response.data);
		})
		.catch(function (error) {
		    console.log(error);
	  	});
    }
}
export default {
	state,
	getters,
	mutations,
	actions
}


